          color: #fff !important;
          margin-top: 30px;
          font-size: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          position: relative;
      }
     
      .flash-across {
          position:absolute;
          animation:changeImg 3s ease 0s;
          top:0;
          width:30%;
          height:100%;
          content:"";
          background:linear-gradient(to right,rgba(255,255,255,0) 0,rgba(255,255,255,.4) 50%,rgba(255,255,255,0) 100%);
          transform:skewX(-45deg);
          animation-iteration-count:infinite;
      }
      .center-text-btn:hover {
          border-color: #00C4F4;
          color: #00C4F4;
      }
      .img-one{
        width: 150px;
        height: 150px;
        position: absolute;
        left: 10%;
        top: 5%;
        -webkit-animation: scale 2.5s ease-in-out infinite;
      }
      @-webkit-keyframes scale{
        0%, 100%{
          -webkit-transform: scale(1);
        }
        50%{
          -webkit-transform: scale(1.5);
        }
      }
      .img-two{
        width: 120px;
        height: 120px;
        position: absolute;
        right:-1%;
        bottom:5%;
        -webkit-animation: roundRule 2.5s ease-in-out infinite;
        -webkit-transform-origin:  top center;
      }
      @-webkit-keyframes roundRule{
        0%, 100%{
            -webkit-transform: rotate(-30deg);
        }
        50%{
          -webkit-transform: rotate(30deg);
        }
      }
      .img-three{
        width: 100px;
        height: 100px;
        position: absolute;
        right:5%;
        top:5%
      }
      
  }
}
