<template>
  <div class="package">
    <div class="rigt">
      <div class="cou flex-middle" @click="hide">
        <img src="~@/assets/image/wap/ca.png" alt="" />
      </div>
      <div class="warp" @click="hide">
        <div
          class="flex-middle item"
          :class="{ active: $route.name === 'Index' }"
          @click="$router.push({ name: 'Index' })"
        >
          <img class="item-icon" src="~@/assets/image/wap/homes.png" alt="" />
          <img class="item-icon" src="~@/assets/image/wap/hone.png" alt="" />
          <div class="title">{{ $t("首頁") }}</div>
        </div>
        
        <div
          class="flex-middle item"
          :class="{ active: $route.name === 'About' }"
          @click="$router.push({ name: 'About' })">
          <img class="item-icon" src="~@/assets/image/wap/liul.png" alt="" />
          <img class="item-icon" src="~@/assets/image/wap/liu.png" alt="" />
          <div class="title">{{ $t("關於") }}</div>
        </div>
        <div
          class="flex-middle item"
          :class="{ active: $route.name === 'Features' }"
          @click="$router.push({ name: 'Features' })">
          <img class="item-icon" src="~@/assets/image/wap/liul.png" alt="" />
          <img class="item-icon" src="~@/assets/image/wap/liu.png" alt="" />
          <div class="title">{{ $t("特征") }}</div>
        </div>
        <div
          class="flex-middle item"
          :class="{ active: $route.name === 'PlaceMent' }"
          @click="$router.push({ name: 'PlaceMent' })">
          <img class="item-icon" src="~@/assets/image/wap/liul.png" alt="" />
          <img class="item-icon" src="~@/assets/image/wap/liu.png" alt="" />
          <div class="title">{{ $t("IDO") }}</div>
        </div>
        <div
          class="flex-middle item"
          :class="{ active: $route.name === 'Tokens' }"
          @click="$router.push({ name: 'Tokens' })">
          <img class="item-icon" src="~@/assets/image/wap/liul.png" alt="" />
          <img class="item-icon" src="~@/assets/image/wap/liu.png" alt="" />
          <div class="title">{{ $t("代幣") }}</div>
        </div>
        <div
          class="flex-middle item"
          :class="{ active: $route.name === 'Roadmap' }"
          @click="$router.push({ name: 'Roadmap' })">
          <img class="item-icon" src="~@/assets/image/wap/liul.png" alt="" />
          <img class="item-icon" src="~@/assets/image/wap/liu.png" alt="" />
          <div class="title">{{ $t("路線圖") }}</div>
        </div>
        
       
        <div>
          <div
            class="flex-middle item"
            :class="{ active: numtype === '8' }"
            @click.stop=""
          >
            <img class="item-icon" src="~@/assets/image/wap/qqa.png" alt="" />
            <img class="item-icon" src="~@/assets/image/wap/qq.png" alt="" />
            <div class="title flex-item">
              {{ local === "en" ? "English" : "繁體中文" }}
            </div>
            <div class="right flex-middle" @click.stop="event('8')">
              <img
                class="icon"
                v-if="numtype === '8'"
                src="~@/assets/image/wap/zen.png"
                alt=""
              />
              <img
                class="icon"
                v-else
                src="~@/assets/image/wap/fan.png"
                alt=""
              />
            </div>
          </div>
          <div v-show="numtype === '8'" class="item-title">
            <div class="text" @click="changeLanguage('zh-tw')">繁體中文</div>
            <div class="text" @click="changeLanguage('en')">English</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { setLang } from "@/utils/i18n";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
export default {
  setup(props, context) {
    const router = useRouter();
    const store = useStore();
    const data = reactive({
      numtype: "",
      local: computed(() => store.state.locale),
      event: type => {
        if (data.numtype === type) {
          return (data.numtype = "");
        }
        data.numtype = type;
      },
      changeLanguage: inject => {
        setLang(inject);
        router.go(0);
      },
      toSwap: () => {
        window.location.href = "/swap";
      },
      hide: () => {
        context.emit("showfun");
      }
    });
    const refDate = toRefs(data);
    return { ...refDate };
  }
};
</script>

<style lang="scss" scoped>
.package {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  background: rgba($color: #000000, $alpha: 0.6);
  z-index: 1111;
  .rigt {
    position: absolute;
    width: 56%;
    height: 100%;
    right: 0;
    background: #0B1D33;
    padding: 57px 0 20px;
    overflow: auto;
    .cou {
      position: absolute;
      height: 57px;
      padding: 0 17px;
      position: absolute;
      right: 0;
      top: 0;
      img {
        width: 13px;
        height: 13px;
      }
    }
    .warp {
      .item {
        height: 48px;
        padding: 0 0 0 18px;
        .item-icon {
          width: 19px;
          height: 19px;
          &:nth-child(1) {
            display: none;
          }
        }
        .title {
          font-size: 15px;
          color: #fff;
          font-weight: bold;
          margin-left: 14.5px;
        }
      }
      .active {
        background: #030B15;
        .item-icon {
          &:nth-child(1) {
            display: block;
          }
          &:nth-child(2) {
            display: none;
          }
        }
      }
    }
  }
}
.right {
  height: 100%;
  padding: 0 20px;
}
.icon {
  width: 7.5px;
  height: 4.5px;
}
.item-title {
  .text {
    font-size: 14px;
    clear: #111;
    font-weight: bold;
    line-height: 48px;
    padding: 0 0 0 52px;
  }
}
</style>
